import { useCallback, useEffect, useState } from "react";
import './Translator.scss';
import { AiOutlineClose } from 'react-icons/ai';
import { BiSolidVolumeFull } from 'react-icons/bi';
//@ts-ignore
import { SayButton }from 'react-say';

const Translator = () => {
  const [inputText, setInputText] = useState('');
  const [outputLang, setOutputLang] = useState('ru');
  const [outputText, setOutputText] = useState('');
  const [isTranslated, setIsTranslated] = useState<boolean>();


//   const volume = outputLang === "ru" ? "ru-Ru" : outputLang === "es" ? "es-Es" : '' 
  const selector = useCallback((voices:any) => [...voices].find(v => v.lang === "ru-Ru"), []);


//   const translate = () => {
    console.log(outputLang);
    const options = {
      method: 'POST',
      headers: {
        'content-type': 'application/json',
        'X-RapidAPI-Key': '41a6c0f2d0mshe52f060738b60dep165ba8jsne45b13573230',
        'X-RapidAPI-Host': 'microsoft-translator-text.p.rapidapi.com'
      },
      body: `[{"Text":"${inputText}"}]`
    };

    const url = `https://microsoft-translator-text.p.rapidapi.com/translate?to%5B0%5D=${outputLang}&api-version=3.0&profanityAction=NoAction&textType=plain`;

    fetch(url, options)
      .then(response => {
        if (response.status !== 200) {
          setIsTranslated(false);
          console.log("there's an error");
          return;
        }
        setIsTranslated(true);
        response.json()
          .then(response => {
            const translatedText = response[0].translations[0].text;
            setOutputText(translatedText);
            console.log(translatedText);
          })
      })
      .catch(err => {
        setIsTranslated(false);
        console.error(err)
      });
//   }

  const clearInput = () => {
    setInputText('');
    setOutputText('');
    setIsTranslated(false);
  }

  return (
    <section className="translator2">
    <div className="rowWrapper2">
      <div className="translatorContainer2 inputLang">
        <div className="topRow">
        
        <select
            name="languages"
            id="languages"
            className="form-select form-select-sm w-100 p-2"
          >
            <option>Detect language</option>
          </select>
        </div>
        <form className="input-form">
          <textarea
            className="textBox2"
            placeholder="Enter text (any language)"
            onChange={e => setInputText(e.target.value)}
            value={inputText}
          >
          </textarea>
          {
            inputText !== "" &&
            <AiOutlineClose
              className="iconBtn closeBtn"
              onClick={clearInput}
            />
          }
        </form>
      </div>
      <div className="translatorContainer2 output-lang">
        <div className="topRow">
          <select
            name="languages"
            id="languages"
            className="form-select form-select-sm w-100 p-2"
            onChange={e => setOutputLang(e.target.value)}
          >
            <option value="ru">Russian</option>
            <option value="ar">Arabic</option>
            <option value="en">English</option>
            <option value="fr">French</option>
            <option value="de">German</option>
            <option value="es">Spanish</option>
            <option value="ko">Korean</option>
            <option value="zh">Chinese(simplified)</option>
            <option value="zh-CHT">Chinese(traditional)</option>
            <option value="it">Italian</option>
          </select>
        </div>
        <p className="textBox2 outputBox">
          {
            isTranslated === false ?
              <span className="output-placeholder translation-error">Translation failed</span>
              :
              outputText === "" ?
                <span className="output-placeholder">Select a language</span>
                :
                outputText
          }
           <SayButton
      speak={"Привет всем"}
      voice={ selector }
    ><BiSolidVolumeFull></BiSolidVolumeFull></SayButton>
          
          
        </p>
      </div>
    </div>

    
  </section>
  );
}

export default Translator;

