import React, { useState, useEffect } from 'react'
import { Worker } from '@react-pdf-viewer/core';
import { Viewer } from '@react-pdf-viewer/core';
import '@react-pdf-viewer/core/lib/styles/index.css';
import { defaultLayoutPlugin } from '@react-pdf-viewer/default-layout';
import '@react-pdf-viewer/default-layout/lib/styles/index.css';
// @ts-ignore
import Rodal from 'rodal';
import 'rodal/lib/rodal.css';
import s from "../MenuContext.module.scss"
import TranslateImg from "../../assets/translation.png"
import Translate from '../Translate/Translate';

interface Props {
    name: any,
    book: any
  }

const Previews = ({name, book}: Props) => {
    const [context, setContext] = React.useState(false);
    const [xYPosistion, setXyPosistion] = React.useState({ x: 0, y: 0 });
    const [chosen, setChosen] = React.useState(false);
    const [show2, setShow2] = useState(false)
    const [show, setShow] = useState(false)
    const defaultLayoutPluginInstance = defaultLayoutPlugin();
    const [highlightedText, setHighlightedText] = useState("");


    const showNav = (event: any) => {
      event.preventDefault();
      setContext(false);
      const positionChange = {
        x: event.pageX,
        y: event.pageY,
      };
      setXyPosistion(positionChange);
      setContext(true);
    };
  
    const hideContext = () => {
      setContext(false);
    };
  

    useEffect(() => {
      const selected = window.getSelection() as Selection
      const saveSelection = () => {
        setHighlightedText(selected.toString());
  
  
      };
      document.addEventListener('mouseup', saveSelection);
      return () => document.removeEventListener('mouseup', saveSelection);
    });
  
  

  
    function close3() {
        setShow2(!show2)
        setChosen(false)
      }

      function close() {
      setShow(!show)
      setChosen(!chosen)
    }
  return (
  <div>
    <button onClick={close3}>{name}</button>
     <Rodal customStyles={{overflowY: "auto", width: "100%", height: "100%"}} animation={"slideLeft"} visible={show2} onClose={close3}>

          <form>
        <Translate inputText={chosen && highlightedText} setChosen={setChosen} chosen={chosen} show={show} setShow={setShow} highLight={setHighlightedText}/>
  
  
      </form>
  
      <h5>Preview PDF</h5>
        
  <div className={s.viewer} onContextMenu={showNav}
        onClick={hideContext}>
  
  {book&&(
    <Worker workerUrl="https://unpkg.com/pdfjs-dist@3.6.172/legacy/build/pdf.worker.min.js">
      <Viewer fileUrl={book}
      plugins={[defaultLayoutPluginInstance]}></Viewer>
    </Worker>
  )}
  
  {!book&&<>No file is selected yet</>}
  





  <div
        className={s.contextContainer}
        onContextMenu={showNav}
        onClick={hideContext}
      >

        {context && (
          <div
            style={{ top: xYPosistion.y, left: xYPosistion.x }}
            className={s.rightClick}
          >
            <div className={s.menuElement} onClick={close}>
              <a href="#" data-tooltip="Translate"><img src={TranslateImg} alt="" /></a>
            </div>
          </div>
        )}
      </div>
  </div>



    </Rodal>
  </div>
   

  )
}

export default Previews
