import React from 'react'
import { NavLink } from 'react-router-dom'
import { AiOutlineMenu, AiOutlineClose } from 'react-icons/ai';
import "./Navbar.scss"
import Image1 from "../assets/door.png"
import Image2 from "../assets/book.png"
import Image3 from "../assets/vocab.png"
import Image4 from "../assets/translate.png"
import Image5 from "../assets/doorClosed.png"


interface Props {
  show1: any
}


const Navbar = ({show1}:Props) => {
const [nav, setNav] = React.useState(false)

function setNavF(i:number) {
  setNav(false)
  show1(i)
}
  return (
    <div>
        <div className="navbar">
        <div className="container nav-container">
                      

 <div className={"box"}>
          <div
            className={`${nav === true ? ["menu-items", "active"].join(' ') : ["menu-items"]} `}
          >
            <li><button style={{background: "white", border: "none"}} onClick={() => setNavF(1)}><a><img style={{width: "40px"}} src={Image1} alt="" /> Начать</a></button></li>
            <li><button style={{background: "white", border: "none"}} onClick={() => setNavF(2)}><a><img style={{width: "40px", marginTop: "-10px"}} src={Image2} alt="" /> Мои книги</a></button></li>
            <li><button style={{background: "white", border: "none"}} onClick={() => setNavF(3)}><a><img style={{width: "40px"}} src={Image3} alt="" /> Cловарь</a></button></li>
            <li><button style={{background: "white", border: "none"}} onClick={() => setNavF(4)}><a><img style={{width: "40px"}} src={Image4} alt="" /> Переводчик</a></button></li>
            <li><button style={{background: "white", border: "none"}} onClick={() => setNavF(5)}><a><img style={{width: "40px"}} src={Image5} alt="" /> Личный Кабинет</a></button></li>
          </div>
          <div onClick={() => setNav(!nav)} className={"mobile_btn"}>
            {nav ? <AiOutlineClose size={35} /> : <AiOutlineMenu size={35} />}
          </div>
        </div>
        </div>
      </div>
       
    </div>
  )
}

export default Navbar