import React, { useState, useRef, useEffect} from 'react'
import "./App.css"
import Navbar from './Navbar/Navbar';
import MenuContext from './Components/MenuContext';
import Loader from './Components/Loader/Loader';
import CustomNav from './Navbar/CustomNav';
import Image from "./assets/door.png"
import Image3 from "./assets/vocab.png"
import Image2 from "./assets/book.png"
import Image4 from "./assets/translate.png"
import Image5 from "./assets/doorClosed.png"
import MyBooks from './Components/Page2/MyBooks';
import Translator from './Components/Page3/Translation';
import Previews from './Components/Page2/Previews';


function App() {
  const [loading, setLoading] = useState<boolean>(false);  
  const [data, setData] = useState<any>([])
  useEffect(() => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  }, []);


    useEffect(() => {
        let newData = localStorage.getItem('data')
        setData(newData? JSON.parse(newData) : [])
    }, [])


    const [comp,setComp] = useState(1)

    function show1(i:number){
        setComp(i)
    }
  return (
    <div className="container">

      <div className='div1'> 
      <CustomNav
      li={[
        ["Начало", Image, 1],
        ["Мои книги", Image2, 2],
        ["Словарь", Image3, 3],
        ["Переводчик", Image4, 4],
        ["Личный кабинет", Image5, 5]
      ]}
      show1={show1}
    />
          <Navbar show1={show1}/> 
        
      </div>
    <div className="div2">
      { loading ? <Loader /> : <div >
                    {
                    comp===1 ? <MenuContext data={data}/> : comp===2 ? <MyBooks data={data}/> : comp===4 ? <Translator /> : ''
                }</div>}
    </div>
     
                    
                    
    </div>
  );
}

export default App;